* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #0B2434;
  padding: 30px;
  font-family: 'Karla', sans-serif;
}
.Main{
  background-color: #F5F5F5;
  height: 680px;
  width: 650px;
  border-radius: 5px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content:space-around;
  align-items: center;
}

.title {
  font-size: 40px;
  margin: 0;
}

.instructions {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  margin-top: 0;
  text-align: center;
  max-width: 58%;
}
.dice-container{
  display: grid;
  grid-template:auto auto / repeat(5,1fr) ;
  gap:20px;
  margin-bottom: 40px;
}

.die-face{
  height: 90px;
  width:95px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.die-num{
  font-size: 3rem;
}

.roll-dice{
  height: 50px;
  width: 130px;
  border: none;
  border-radius: 6px;
  background-color: #5035FF;
  color: white;
  font-size: 1.2rem;
  font-family: 'Karla', sans-serif;
}

.roll-dice:focus {
    outline: none;
}

.roll-dice:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}